import React, { useState, useEffect } from "react";
import "./style.css";
import axios from "axios";
import InputMask from "react-input-mask";
import { BASE_URL } from "../../../config/dev";
import Footer from "../../../components/Footer";
import ModalDetalhes from "../../../components/ModalDetalhesResp";
import ModalComunicado from "../../../components/ModalComunicadoResp";

const Responsaveis = (props) => {
  const [list, setList] = useState([]);
  const [editOrNot, setEditOrNot] = useState([]);

  useEffect(() => {
    getUser();
  }, []);

  const getUser = () => {
    axios({
      method: "get",
      url: `${BASE_URL}pais/getPais.php`,
    })
      .then(function (response) {
        // console.log("resp get:");
        // console.log(response.data);
        // console.log(JSON.parse(response.data));
        let data = response.data;
        let str = data.split("next,");
        let json = [];
        // console.log("list responsaveis str");
        // console.log(str);

        for (let i = 0; i < str.length - 1; i++) {
          let registro = str[i].split(",");
          // console.log("registro");
          // console.log(registro);
          // os que possuem a nomenclatura "2" indica o filho secundaria para o atual registro
          json.push({
            id: registro[0],
            cpf: registro[1],
            senha: registro[2],
            nome_resp1: registro[3],
            nome_resp2: registro[4],
            tel1: registro[5],
            tel2: registro[6],
            // 1º filho
            matricula_aluno: registro[7],
            nome_aluno: registro[8],
            turma_aluno: registro[9],
            ano_letivo_aluno: registro[10],
            // 2º filho
            matricula_aluno2: registro[12],
            nome_aluno2: registro[13],
            turma_aluno2: registro[14],
            ano_letivo_aluno2: registro[15],

            nivel: registro[11],
          });
        }
        setList(json);
      })
      .catch(function (response) {
        // console.log(response);
      });
  };

  const edit = (e, id, numInput) => {
    document.getElementById("btn_edit_" + id).style.display = "none";
    document.getElementById("btn_confirm_edit_" + id).style.display = "block";
    switch (numInput) {
      case 1:
        // document.getElementById(`resp1_${id}`).style.display = "none";
        document.getElementById(`resp1_hidden_${id}`).style.display = "block";
    }
  };
  const save = (e, id, idRegistro) => {
    // ?
  };
  const editHidden = (e, i, valueInput) => {};

  const listPais = (turma) => {
    let rows = [];
    // console.log("list");
    // console.log(list);

    for (let i = 0; i < list.length; i++) {
      // VERIFICAR AQUI
      if (list[i].turma_aluno === turma || list[i].turma_aluno2 === turma) {
        rows.push(
          <tr key={i}>
            <td>{list[i].nome_resp1}</td>
            <td>{list[i].nome_resp2}</td>
            <td>{list[i].cpf}</td>
            {/* <td>{list[i].senha}</td> */}
            <td>{list[i].tel1}</td>
            <td>
              {list[i].turma_aluno2 !== turma
                ? list[i].nome_aluno
                : list[i].nome_aluno2}
            </td>
            <td>
              {list[i].turma_aluno2 !== turma
                ? list[i].matricula_aluno
                : list[i].matricula_aluno2}
            </td>
            <td className="acoes-responsaveis-list">
              <form
                id={`form_delete_${i}`}
                className=""
                method="post"
                action={`${BASE_URL}pais/deletePais.php`}
                onSubmit={(e) => {
                  if (
                    window.confirm(
                      "Tem certeza que deseja excluir esse registro?"
                    )
                  ) {
                    // console.log("excluido!");
                  } else {
                    e.preventDefault();
                  }
                }}
              >
                <input
                  type="hidden"
                  name={`id_delete`}
                  id={`id_delete_${i}`}
                  value={list[i].id}
                />
                <div className="center">
                  <button
                    title="EXCLUIR"
                    type="submit"
                    className="waves-effect waves-light btn btn-delete red"
                  >
                    <i class="material-icons">delete</i>
                  </button>
                </div>
              </form>
              <ModalDetalhes
                details={list[i]}
                code={parseInt(Math.random() * 1000000000000)}
              />
              <ModalComunicado
                details={list[i]}
                code={parseInt(Math.random() * 1000000000000)}
              />
            </td>
          </tr>
        );
      }
    }
    return rows;
  };
  const tabelaListagemPais = (turma) => {
    return (
      <div className="container-table-resp">
        <table className="table-list-responsaveis responsive-table">
          <thead>
            <tr>
              <th>Responsável (1)</th>
              <th>Responsável (2)</th>
              <th>CPF</th>
              {/* <th>Senha</th> */}
              <th>Telefone</th>
              <th>Nome Aluno</th>
              <th>Matrícula Aluno</th>
              <th>Ações</th>
            </tr>
          </thead>

          <tbody>{listPais(turma)}</tbody>
        </table>
      </div>
    );
  };

  const alternaTurma = (e, item) => {
    ocultaAllTurma();
    switch (item) {
      case 1:
        document.getElementById("bercario").style.display = "block";
        break;
      case 2:
        document.getElementById("mini-maternal").style.display = "block";
        break;
      case 3:
        document.getElementById("maternal").style.display = "block";
        break;
      case 4:
        document.getElementById("infantilI").style.display = "block";
        break;
      case 5:
        document.getElementById("infantilII").style.display = "block";
        break;
      case 6:
        document.getElementById("infantilIII").style.display = "block";
        break;
      case 7:
        document.getElementById("ano-1").style.display = "block";
        break;
      case 8:
        document.getElementById("ano-2").style.display = "block";
        break;
      case 9:
        document.getElementById("ano-3").style.display = "block";
        break;
      case 10:
        document.getElementById("ano-4").style.display = "block";
        break;
      case 11:
        document.getElementById("ano-5").style.display = "block";
        break;
    }
  };

  const ocultaAllTurma = () => {
    document.getElementById("bercario").style.display = "none";
    document.getElementById("mini-maternal").style.display = "none";
    document.getElementById("maternal").style.display = "none";
    document.getElementById("infantilI").style.display = "none";
    document.getElementById("infantilII").style.display = "none";
    document.getElementById("infantilIII").style.display = "none";
    document.getElementById("ano-1").style.display = "none";
    document.getElementById("ano-2").style.display = "none";
    document.getElementById("ano-3").style.display = "none";
    document.getElementById("ano-4").style.display = "none";
    document.getElementById("ano-5").style.display = "none";
  };

  const renderNivelAccess = () => {
    let retorno = null;
    if (props.nivel == 1) {
      retorno = (
        <>
          <section id="responsaveis-adm">
            <h2 className="">Listagem de Responsáveis</h2>

            <nav class="nav-extended nav-turmas-resp">
              <div class="nav-content">
                <ul class="tabs tabs-transparent">
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 1)}>Berçário</a>
                  </li>
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 2)}>Mini Maternal</a>
                  </li>
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 3)}>Maternal</a>
                  </li>
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 4)}>Infantil I</a>
                  </li>
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 5)}>Infantil II</a>
                  </li>
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 6)}>Infantil III</a>
                  </li>
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 7)}>1º ano</a>
                  </li>
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 8)}>2º ano</a>
                  </li>
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 9)}>3º ano</a>
                  </li>
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 10)}>4º ano</a>
                  </li>
                  <li class="tab">
                    <a onClick={(e) => alternaTurma(e, 11)}>5º ano</a>
                  </li>
                </ul>
              </div>
            </nav>

            <div id="bercario" class="col s12">
              <div className="center">
                <h5>Berçário</h5>
              </div>
              {tabelaListagemPais("Berçário")}
            </div>
            <div id="mini-maternal" className="col s12 display-none">
              <div className="center">
                <h5>Mini Maternal</h5>
              </div>
              {tabelaListagemPais("Mini Maternal")}
            </div>
            <div id="maternal" class="col s12 display-none">
              <div className="center">
                <h5>Maternal</h5>
              </div>
              {tabelaListagemPais("Maternal")}
            </div>
            <div id="infantilI" class="col s12 display-none">
              <div className="center">
                <h5>Infantil I</h5>
              </div>
              {tabelaListagemPais("Infantil I")}
            </div>
            <div id="infantilII" class="col s12 display-none">
              <div className="center">
                <h5>Infantil II</h5>
              </div>
              {tabelaListagemPais("Infantil II")}
            </div>
            <div id="infantilIII" class="col s12 display-none">
              <div className="center">
                <h5>Infantil III</h5>
              </div>
              {tabelaListagemPais("Infantil III")}
            </div>
            <div id="ano-1" class="col s12 display-none">
              <div className="center">
                <h5>1º ano</h5>
              </div>
              {tabelaListagemPais("1º ano")}
            </div>
            <div id="ano-2" class="col s12 display-none">
              <div className="center">
                <h5>2º ano</h5>
              </div>
              {tabelaListagemPais("2º ano")}
            </div>
            <div id="ano-3" class="col s12 display-none">
              <div className="center">
                <h5>3º ano</h5>
              </div>
              {tabelaListagemPais("3º ano")}
            </div>
            <div id="ano-4" class="col s12 display-none">
              <div className="center">
                <h5>4º ano</h5>
              </div>
              {tabelaListagemPais("4º ano")}
            </div>
            <div id="ano-5" class="col s12 display-none">
              <div className="center">
                <h5>5º ano</h5>
              </div>
              {tabelaListagemPais("5º ano")}
            </div>
          </section>
        </>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  };

  return renderNivelAccess();
};

export default Responsaveis;
