import React, { useEffect, useState } from "react";
import "./style.css";
import HeaderAdm from "../../../components/HeaderAdm";
import axios from "axios";
import { BASE_URL } from "../../../config/dev";
import Header from "../../../components/HeaderAdm";
import Footer from "../../../components/Footer";

const ComunicadosResp = (props) => {
  const [ni, setNi] = useState(window.localStorage.getItem("nivel"));
  const [idUser, setIdUser] = useState(window.localStorage.getItem("cod"));
  const [listUserComunicado, setListUserComunicado] = useState([]);
  const [listUser, setListUser] = useState([]);
  const [viewAluno1, setViewAluno1] = useState(true);

  useEffect(() => {
    const nivel = window.localStorage.getItem("nivel");
    if (nivel != 1 && nivel != 0) {
      window.location.href = "/";
    }
    setNi(nivel);
    getUserComunicado();
    getUser();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const getUserComunicado = () => {
    axios({
      method: "get",
      url: `${BASE_URL}comunicados/getComunicados.php?cod=${idUser}`,
    })
      .then(function (response) {
        let data = response.data;
        let str = data.split("next,");
        let json = [];
        // console.log("str");
        // console.log(str);

        for (let i = 0; i < str.length - 1; i++) {
          let registro = str[i].split("@#$");
          if (registro[2] === ",") {
            json.push({
              id: registro[0],
              id_user: registro[1],
              // typeImg: registro[2],
              img: "",
              text: registro[3],
              createdAt: registro[4],
              title: registro[5],
            });
          } else {
            json.push({
              id: registro[0],
              id_user: registro[1],
              // typeImg: registro[2],
              img: registro[2].slice(1),
              text: registro[3],
              createdAt: registro[4],
              title: registro[5],
            });
          }
        }
        setListUserComunicado(json);
        console.log("json comunic");
        console.log(json);
      })
      .catch(function (response) {});
  };

  const getUser = () => {
    axios({
      method: "get",
      url: `${BASE_URL}pais/getPaisById.php?cod=${idUser}`,
    })
      .then(function (response) {
        let data = response.data;
        let str = data.split("next,");
        let json = [];
        // console.log(str);

        for (let i = 0; i < str.length - 1; i++) {
          let registro = str[i].split(",");
          json.push({
            id: registro[0],
            cpf: registro[1],
            senha: registro[2],
            nome_resp1: registro[3],
            nome_resp2: registro[4],
            tel1: registro[5],
            tel2: registro[6],
            // 1º filho
            matricula_aluno: registro[7],
            nome_aluno: registro[8],
            turma_aluno: registro[9],
            ano_letivo_aluno: registro[10],
            // 2º filho
            matricula_aluno2: registro[12],
            nome_aluno2: registro[13],
            turma_aluno2: registro[14],
            ano_letivo_aluno2: registro[15],

            nivel: registro[11],
          });
        }
        console.log("json[0] home resp");
        console.log(json[0]);
        setListUser(json[0]);
      })
      .catch(function (response) {
        // console.log(response);
      });
  };

  const renderizaComunicado = () => {
    const itens = listUserComunicado;

    let rows = [];
    for (let i = 0; i < itens.length; i++) {
      const iframe = () => {
        let respDoc = null;
        if (itens[i].img !== "") {
          respDoc = (
            <iframe
              title={`Arquivo ${i}`}
              className="img-comunicados get-file"
              src={`${itens[i].img}`}
              height="600"
              width="730"
            ></iframe>
          );
        } else {
          respDoc = <div></div>;
        }
        return respDoc;
      };
      console.log("render");
      // listUser.turma_aluno === itens[i].title
      //   ? setViewAluno1(true)
      //   : setViewAluno1(false);

      if (viewAluno1 && itens[i].title === listUser.turma_aluno) {
        rows.push(
          <div className="card-comunica-resp" key={i}>
            <div>
              <b className="mr-10">Comunicado turma {itens[i].title}:</b>
              {itens[i].createdAt}
            </div>
            <div dangerouslySetInnerHTML={{ __html: itens[i].text }} />
            <div className="container-img-comunica">{iframe()}</div>
            <br />
          </div>
        );
      } else if (!viewAluno1 && itens[i].title === listUser.turma_aluno2) {
        rows.push(
          <div className="card-comunica-resp" key={i}>
            <div>
              <b className="mr-10">Comunicado turma {itens[i].title}:</b>{" "}
              {itens[i].createdAt}
            </div>
            <div dangerouslySetInnerHTML={{ __html: itens[i].text }} />
            <div className="container-img-comunica">{iframe()}</div>
            <br />
          </div>
        );
      }
    }
    return rows;
  };

  const renderOptionsAlunos = () => {
    let arr = [];
    const optionsValue = [
      "Berçário",
      "Mini Maternal",
      "Maternal",
      "Infantil I",
      "Infantil II",
      "Infantil III",
      "1º ano",
      "2º ano",
      "3º ano",
      "4º ano",
      "5º ano",
    ];
    for (let i = 0; i < optionsValue.length; i++) {
      const label =
        optionsValue[i] === listUser.turma_aluno
          ? listUser.nome_aluno
          : listUser.nome_aluno2;
      if (
        optionsValue[i] === listUser.turma_aluno ||
        optionsValue[i] === listUser.turma_aluno2
      ) {
        arr.push(<option value={optionsValue[i]}>{label}</option>);
      }
    }
    console.log("arr");
    console.log(arr);
    const response = arr.map((item, index) => arr[index]);
    return response;
  };

  const filterAlunos = (e) => {
    const { value } = e.target;
    setViewAluno1(!viewAluno1);
  };

  const infoComunicados = () => {
    return (
      <div className="container-comunicados-resp">
        <h2 className="margin-top-adm">Comunicados</h2>
        <div className="container-itens-comunicado-resp center">
          <div className="d-flex">
            <label for="aluno_filter" className="filter-title">
              Filtro aluno:
            </label>
            <div className="input-field filter-select"></div>
            <select
              id="aluno_filter"
              name="aluno_filter"
              className="turma-select browser-default"
              onChange={(e) => filterAlunos(e)}
            >
              {renderOptionsAlunos()}
            </select>
          </div>
          <hr />
          {renderizaComunicado()}
        </div>
      </div>
    );
  };

  const renderNivelAccess = () => {
    let retorno = null;
    if (props.nivel == 0) {
      retorno = (
        <>
          <section className="home-resp">
            <Header nivel={props.nivel} userName={props.userName} />

            {infoComunicados()}
          </section>
        </>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  };

  return renderNivelAccess();
};

export default ComunicadosResp;
