import React, { Component } from "react";
import M from "materialize-css";
import "./style.css";
import "materialize-css/dist/css/materialize.min.css";
import { BASE_URL } from "../../config/dev";
import CKEditor from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import axios from "axios";

class ModalDetalhesResp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      textComunicado: "",
      listComunicado: {},
    };
  }

  async componentDidMount() {
    await this.getComunicado();
    const options = {
      onOpenStart: () => {
        // console.log("Open Start");
      },
      onOpenEnd: () => {
        // console.log("Open End");
      },
      onCloseStart: () => {
        // console.log("Close Start");
      },
      onCloseEnd: () => {
        // console.log("Close End");
      },
      inDuration: 250,
      outDuration: 250,
      opacity: 0.5,
      dismissible: false,
      startingTop: "4%",
      endingTop: "10%",
    };
    M.Modal.init(this.Modal, options);
    // console.log("state com json convertido");
    // console.log(this.state.listComunicado);
  }

  getComunicado() {
    axios({
      method: "get",
      url: `${BASE_URL}comunicados/getComunicados.php?cod=${this.props.details.id}`,
    }).then((response) => {
      let data = response.data;
      let str = data.split("next,");
      // console.log("str modals");
      // console.log(str);
      let json = [];
      for (let i = 0; i < str.length - 1; i++) {
        let registro = str[i].split("@#$");
        if (registro[2] === ",") {
          json.push({
            id: registro[0],
            id_user: registro[1],
            // typeImg: registro[2],
            img: "",
            text: registro[3],
            createdAt: registro[4],
            title: registro[5],
          });
        } else {
          json.push({
            id: registro[0],
            id_user: registro[1],
            // typeImg: registro[2],
            img: registro[2].slice(1),
            text: registro[3],
            createdAt: registro[4],
            title: registro[5],
          });
        }
      }
      // console.log("json modal");
      // console.log(json);
      this.setState({
        listComunicado: json,
      });
      // console.log("testessss 33333");
      // console.log(this.state.listComunicado);
    });
  }

  exibeImgComunicado(img) {
    let retorno = null;
    if (img != "") {
      retorno = (
        <div className="center container-img-comunicados">
          <iframe
            className="img-comunicados"
            src={`${img}`}
            height="300"
            width="530"
          ></iframe>

          {/* <img
            className="img-comunicados responsive-img"
            src={`${type},${img}`}
          /> */}
        </div>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  }

  renderizaComunicado() {
    // console.log("renderiza comunicado");
    // console.log(this.state.listComunicado);
    const itens = this.state.listComunicado;
    // console.log(itens);
    let rows = [];
    for (let i = 0; i < itens.length; i++) {
      rows.push(
        <div className="card-comunicado" key={i}>
          <div className="container-title-comuni">
            <b>Comunicado turma {itens[i].title}:</b> {itens[i].createdAt}
          </div>
          <div dangerouslySetInnerHTML={{ __html: itens[i].text }} />
          {this.exibeImgComunicado(itens[i].img)}
          <br />

          <form
            id={`form_delete_comunicado_${i}`}
            className=""
            method="post"
            action={`${BASE_URL}comunicados/deleteComunicado.php`}
            onSubmit={(e) => {
              if (
                window.confirm(
                  "Tem certeza que deseja excluir esse comunicado?"
                )
              ) {
                // console.log("excluido!");
              } else {
                e.preventDefault();
              }
            }}
          >
            <input
              type="hidden"
              name={`id_delete`}
              id={`id_delete_${i}`}
              value={itens[i].id}
            />
            <div className="center btn-delete-comunicado">
              <button
                title="EXCLUIR"
                type="submit"
                className="waves-effect waves-light btn btn-delete red"
              >
                <i class="material-icons">delete</i>
              </button>
            </div>
          </form>
        </div>
      );
    }
    return rows;
  }

  convertBase64(e, element, id, code) {
    var filesSelected = document.getElementById(element).files;
    if (filesSelected.length > 0) {
      var fileToLoad = filesSelected[0];
      var fileReader = new FileReader();

      fileReader.onload = function (fileLoadedEvent) {
        var srcData = fileLoadedEvent.target.result; // <--- data: base64
        // console.log("srcData");
        // console.log(srcData);
        document.getElementById(
          `mural_img_comunicado_${id}_${code}`
        ).value = srcData;
        // rgBase64 = srcData;
        // document.getElementById("rgCandidatoHidden").value = srcData;
      };
      fileReader.readAsDataURL(fileToLoad);
    }
  }

  getDate() {
    const date = new Date();
    const dia = `00${date.getDate()}`;
    const mes = `00${date.getMonth() + 1}`;
    const ano = date.getFullYear();
    const fullDate = `${dia.slice(-2)}/${mes.slice(-2)}/${ano}`;
    return fullDate;
  }

  renderOptionsTurma() {
    let arr = [];
    const optionsValue = [
      "Berçário",
      "Mini Maternal",
      "Maternal",
      "Infantil I",
      "Infantil II",
      "Infantil III",
      "1º ano",
      "2º ano",
      "3º ano",
      "4º ano",
      "5º ano",
    ];
    for (let i = 0; i < optionsValue.length; i++) {
      if (
        optionsValue[i] === this.props.details.turma_aluno ||
        optionsValue[i] === this.props.details.turma_aluno2
      ) {
        arr.push(<option value={optionsValue[i]}>{optionsValue[i]}</option>);
      }
    }
    console.log("arr");
    console.log(arr);
    const response = arr.map((item, index) => arr[index]);
    return response;
  }

  render() {
    return (
      <>
        <a
          title="COMUNICADO"
          className="waves-effect waves-light btn modal-trigger green btn-margin-left"
          data-target={`modal_resp_comunicado_${this.props.details.id}_${this.props.code}`}
        >
          <i class="material-icons">import_contacts</i>
        </a>

        <div
          ref={(Modal) => {
            this.Modal = Modal;
          }}
          id={`modal_resp_comunicado_${this.props.details.id}_${this.props.code}`}
          className="modal"
        >
          <form
            className="row"
            method="post"
            action={`${BASE_URL}comunicados/postComunicados.php`}
          >
            <div className="modal-content">
              <h4>Comunicado do responsável {this.props.details.nome_resp1}</h4>
              {/* <div>id: {this.props.details.id}</div> */}
              <CKEditor
                editor={ClassicEditor}
                onInit={(editor) => {
                  // You can store the "editor" and use when it is needed.
                  // console.log("Editor is ready to use!", editor);
                }}
                onChange={(event, editor) => {
                  const data = editor.getData();
                  // console.log("onchange event");
                  // console.log({ event, editor, data });
                  this.state.textComunicado = data;
                  // console.log(this.state.textComunicado);
                  document.getElementById(
                    `mural_text_comunicado_${this.props.details.id}_${this.props.code}`
                  ).value = data;
                }}
              />
              <input
                name="mural_text_comunicado"
                id={`mural_text_comunicado_${this.props.details.id}_${this.props.code}`}
                type="hidden"
              />
              <input
                name="mural_img_comunicado"
                id={`mural_img_comunicado_${this.props.details.id}_${this.props.code}`}
                type="hidden"
              />
              <input
                name="mural_id_user"
                id="mural_id_user"
                value={this.props.details.id}
                type="hidden"
              />
              <input
                name="createdAt"
                id="createdAt"
                value={this.getDate()}
                type="hidden"
              />

              <div class="file-field input-field">
                <div class="btn orange">
                  <span>PDF</span>
                  <input
                    type="file"
                    className=""
                    id={`file_comunicado_${this.props.details.id}_${this.props.code}`}
                    onChange={(e) => {
                      this.convertBase64(
                        e,
                        `file_comunicado_${this.props.details.id}_${this.props.code}`,
                        this.props.details.id,
                        this.props.code
                      );
                    }}
                    name="file_comunicado"
                  />
                </div>
                <div class="file-path-wrapper">
                  <input
                    className="file-path validate input-file"
                    type="text"
                    placeholder="SELECIONE..."
                  />
                </div>
              </div>

              <div class="input-field col s12 m5">
                <select
                  id="turma_title"
                  name="turma_title"
                  className="turma-select browser-default"
                >
                  {/* <option value="selecione" selected>
                    Escolha a turma
                  </option> */}
                  {this.renderOptionsTurma()}
                </select>
              </div>
            </div>
            <div class="modal-footer">
              <div className="modal-close waves-effect waves-red btn red mr-2">
                CANCELAR
              </div>
              <button
                type="submit"
                className="modal-close waves-effect waves-green btn green btn-confirm-comunicado"
              >
                CONFIRMAR
              </button>
            </div>
          </form>

          {this.renderizaComunicado()}
        </div>
      </>
    );
  }
}

export default ModalDetalhesResp;
